import axios from "axios";
import store from "@/store";
let request;
const root = process.env.API_URL
function formatUrl(url) {
  return root + url;
}
export default function createOther(re) {
  request = re;
}
const instance = axios.create({
  withCredentials: true
});
const instance2 = axios.create({
  withCredentials: true
});
let lastCaptchHeader = "";
instance.interceptors.response.use(
  ({ data: { code, data } }) => {
    if (code !== 200) {
      console.error("error in other " + code);
    }
    return data;
  },
  data => {
    console.log(data);
  }
);

// 获取手机验证码
export async function getSmsCode(data) {
  const { captcha_code, captcha_flag, user_name, captcha_key } = data
  return request(formatUrl('/api/common/sms/ajax_send_sms_code'), {
    method: "post",
    noWrap:true,
    data: {
      captcha_code,
      captcha_key,
      captcha_flag,
      user_name
    }
  });
}

// 快捷登录
export async function goLogin(data) {
  const { user_name, sms_code, true_name, jxc_p_id } = data
  return request(formatUrl('/api/drive/user/ajax_sms_login'), {
    method: "post",
    showError:false,
    data: {
      user_name,
      sms_code,
      true_name,
      jxc_p_id
    }
  });
}

// 退出
export async function goLogout() {
  return request(formatUrl('/api/drive/user/ajax_wechat_logout'), {
    method: "post"
  })
}

// ceshi
export async function ceshi() {
  return request('/api/test2', {
    method: "post"
  });
}

export async function getCaptcha() {
  return instance2("/exchange/codes/captcha", {
    responseType: "blob"
  }).then(response => {
    return new Promise(resolve => {
      lastCaptchHeader = response.headers["ucex-captcha"];
      var reader = new window.FileReader();
      reader.readAsDataURL(response.data);
      reader.onload = function () {
        resolve({
          captchaHeader: lastCaptchHeader,
          data: reader.result
        });
      };
    });
  });
}

export async function queryAnnouncement() {
  return instance2(`/ucex-controller/announcement/pc`).then(({ data }) => {
    const { data: pData, result, code } = data;
    if (result == "Success" || code == 0) {
      return pData;
    }
  });
}


