let request
const root = process.env.API_URL
function formatUrl(url) {
  return root + url;
}
export default function createExchange(rt) {
  request = rt
}

// 登录
export async function goLogin(data) {
  const { user_name, password } = data
  return request(formatUrl("/api/qmsj/ajax_login"), {
    method: "POST",
    showError: true,
    data: {
      user_name,
      password
    }
  })
}

export async function getAllowProjectList(data) {
  return request(formatUrl("/api/qmsj/ajax_get_allow_project_list"), {
    method: "POST",
    showError: true
  })
}

// 获取项目信息
export async function getProjectInfo(data) {
  const { qmsj_project_id } = data
  return request(formatUrl("/api/qmsj/ajax_get_project_info"), {
    method: "POST",
    showError: true,
    data: {
      qmsj_project_id
    }
  })
}

export async function upload(data) {
  const file = data
  return request(formatUrl("/api/qmsj/ajax_upload_attachment"), {
    method: "POST",
    showError: true,
    dataType: 'json',
    cache: false,
    processData: false,
    contentType: false,
    data: file,
    headers: {
      'Content-Type': 'multipart/form-data'// ;boundary = ' + new Date().getTime()
    }
  })
}

// 提交问卷
export async function uploadRespondent(data) {
  const { qmsj_project_info, attachment_list } = data
  return request(formatUrl("/api/qmsj/ajax_upload_respondent"), {
    method: "POST",
    showError: true,
    data: {
      qmsj_project_info,
      attachment_list
    }
  })
}

//查看提交问卷列表
export async function getReadRespondentList(data) {
  const { page, count, qmsj_project_title_filter, qmsj_people_name_filter, start_date_filter, end_date_filter } = data
  return request(formatUrl("/api/qmsj/ajax_get_read_respondent_list"), {
    method: "POST",
    data: {
      page,
      count,
      qmsj_project_title_filter,
      qmsj_people_name_filter,
      start_date_filter,
      end_date_filter
    }
  })
}

// 查看详情
export async function getReadRespondentDetails(data) {
  const { qmsj_respondent_id } = data
  return request(formatUrl("/api/qmsj/ajax_get_respondent_info"), {
    method: "POST",
    data: {
      qmsj_respondent_id
    }
  })
}

// 提交评论
export async function uploadComment(data) {
  const { qmsj_respondent_id, comment } = data
  return request(formatUrl("/api/qmsj/ajax_upload_respondent_comment"), {
    method: "POST",
    data: {
      qmsj_respondent_id,
      comment
    }
  })
}

// 获取指定评论
export async function getReadRespondentCommentList(data) {
  const { qmsj_respondent_id, page, count } = data
  return request(formatUrl("/api/qmsj/ajax_get_respondent_comment_list"), {
    method: "POST",
    data: {
      qmsj_respondent_id,
      page,
      count
    }
  })
}



