import Vue from 'vue'
import Router from 'vue-router'
Vue.use(Router)

const index = r =>
  require.ensure([], () => r(require("@/view/index")), "index")
const login = r =>
  require.ensure([], () => r(require("@/view/login/login")), "login")
const ask = r =>
  require.ensure([], () => r(require("@/view/ask/ask")), "ask")
const UploadImg = r =>
  require.ensure([], () => r(require("@/view/ask/form/imgUpLoad")), "UploadImg")
  const askList = r =>
  require.ensure([], () => r(require("@/view/ask/askList")), "askList")
  const askDetails = r =>
  require.ensure([], () => r(require("@/view/ask/askDetails")), "askDetails")
const routes = [
  {
    path: '/',
    name: 'index',
    component: index,
    meta: {
      title: '首页',
      requireAuth: true // 需要登录
    }
  },
  {
    path: '/login',
    name: 'login',
    component: login,
    meta: {
      title: "登录"
    }
  },
  {
    path: '/ask/:id',
    name: 'ask',
    component: ask,
    meta: {
      title: "问卷调查",
      requireAuth: true
    }
  },
  {
    path: '/asklist',
    name: 'askList',
    component: askList,
    meta: {
      title: "问卷列表",
      requireAuth: true
    }
  },
  {
    path: '/askdetails/:id',
    name: 'askDetails',
    component: askDetails,
    meta: {
      title: "问卷详情",
      requireAuth: true
    }
  },
  {
    path: '/UploadImg',
    name: 'UploadImg',
    component: UploadImg,
    meta: {
      title: "图片上传"
    }
  }
]

const router = new Router({
  routes,
  scrollBehavior(to, from, savedPosition) {
    if (savedPosition) {
      return savedPosition
    } else {
      return { x: 0, y: 0 }
    }
  }
})

export default router



