const getters = {
  userInfo: state => state.washcar.userInfo,
  token: state => state.washcar.token,
  brandCarFromId: state => state.washcar.brandCarFromId,
  vehicleComparison: state => state.washcar.vehicleComparison,
  vehicleComparisonAdd: state => state.washcar.vehicleComparisonAdd,
  recommenderID: state => state.washcar.recommender,
  getPhone: state => state.washcar.phone
}
export default getters;



