// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
import store from './store/index'
import "./assets/js/rem"
import "mint-ui/lib/style.css"
import "./assets/css/common.css"
import preview from 'vue-photo-preview'
import 'vue-photo-preview/dist/skin.css'
import {
  Swipe, SwipeItem, Navbar, TabItem, TabContainer, TabContainerItem, IndexList, IndexSection, Spinner,
  Popup, Checklist, InfiniteScroll, Picker, Cell, DatetimePicker, Actionsheet, Button, Field
} from 'mint-ui'

import 'viewerjs/dist/viewer.css'
import Viewer from 'v-viewer'
Vue.use(Viewer)

// import VueAwesomeSwiper from 'vue-awesome-swiper'
// import 'swiper/dist/css/swiper.css'
// Vue.use(VueAwesomeSwiper)
Vue.use(InfiniteScroll)
Vue.use(preview)
Vue.component(Swipe.name, Swipe)
Vue.component(SwipeItem.name, SwipeItem)
Vue.component(Navbar.name, Navbar)
Vue.component(TabItem.name, TabItem)
Vue.component(TabContainer.name, TabContainer)
Vue.component(TabContainerItem.name, TabContainerItem)
Vue.component(IndexList.name, IndexList)
Vue.component(IndexSection.name, IndexSection)
Vue.component(Spinner.name, Spinner)
Vue.component(Popup.name, Popup)
Vue.component(Checklist.name, Checklist)
Vue.component(Picker.name, Picker)
Vue.component(Cell.name, Cell);
Vue.component(DatetimePicker.name, DatetimePicker);
Vue.component(Actionsheet.name, Actionsheet);
Vue.component(Button.name, Button);
Vue.component(Field.name, Field);
Vue.config.productionTip = false

Vue.mixin({
  data() {
    return {
    }
  }
})

router.beforeEach(async (to, from, next) => {
  let isLogin = store.state.washcar.isLogin
  let metaRequireAuth = to.meta.requireAuth
  let toName = to.name
  // let toPath = to.path
  // let metaTitle = to.meta.title
  if (metaRequireAuth) { // 是否需要登录权限
    if (isLogin != 1) { // 通过vuex state获取当前的isLogin==1
      localStorage.setItem('beforeLoginUrl', to.fullPath) // 保存用户进入的url
      next({
        path: '/login'
      })
      return false
    } else {
      next();
    }
  } else {
    if (toName == 'login' && isLogin == 1) {
      next('/')
      return false
    } else {
      next();
    }
  }
  next()
})

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  store,
  components: { App },
  template: '<App/>'
})



