const washcar = {
  state: {
    userInfo: '',
    isLogin: 0
  },
  mutations: {
    LOGIN(state, val) {
      state.userInfo = val
    },
    LOGOUT(state) {
      state.userInfo = ''
      state.isLogin = 0
      localStorage.clear()
    },
    ISLOGIN(state, val) {
      state.isLogin = val
    }
  },
  actions: {
    login({ commit }, val) {
      commit("LOGIN", val)
    },
    logout({ commit }) {
      commit("LOGOUT")
    },
    isLogin({ commit }, val) {
      commit("ISLOGIN", val)
    }
  }
}
export default washcar



